.slideshow-container{
    max-width:1000px;
    position:relative;
    margin:auto
}
.mySlides{
    display:none
}
.next,.prev{
    cursor:pointer;
    position:absolute;
    top:50%;
    width:auto;
    margin-top:-22px;
    padding:16px;
    color:#fff;
    background: rgba(255,255,255,.2);
    font-weight:700;
    font-size:18px;
    transition:.6s ease;
    border-radius:0 3px 3px 0;
    box-shadow: inset 0 0px 0 #005380;
}
.numbertext,.text{
    color:#f2f2f2;
    padding:8px 12px;
    position:absolute
}
.next{
    right:0;
    border-radius:3px 0 0 3px
}
.next:hover,.prev:hover{
    background-color:rgba(#005380, 0.8);
    box-shadow: inset 0 0px 0 #005380;
}
.text{
    font-size:15px;
    bottom:8px;
    width:100%;
    text-align:center
}
.numbertext{
    font-size:12px;
    top:0
}
.dot{
    cursor:pointer;
    height:15px;
    width:15px;
    margin:0 2px;
    background-color: #ccc;
    border-radius:50%;
    display:inline-block;
    transition:background-color .6s ease
}
.active,.dot:hover{
    background-color: #005380;
}
.fade{
    -webkit-animation-name:fade;
    -webkit-animation-duration:1.5s;
    animation-name:fade;
    animation-duration:1.5s
}
@-webkit-keyframes fade{
    from{
        opacity:.4
    }
    to{
        opacity:1
    }
}
@keyframes fade{
    from{
        opacity:.4
    }
    to{
        opacity:1
    }
}
